.Account {
  max-width: 1000px;
  margin: 0 auto;
  > .title {
    display: flex;
    align-items: center;
    > h1 {
      flex: 1;
    }

    > button {
      height: 40px;
      padding: 10px 20px;
      border: none;
      background: black;
      color: white;
      border-radius: 5px;
      cursor: pointer;
    }
  }

  > .update-box {
    border: 1px solid grey;
    background: white;
    padding: 30px;
    border-radius: 5px;

    > .input-group {
      display: flex;
      margin-bottom: 10px;
      align-items: center;
      
      > span {
        width: 100px;
        display: inline-block;
      }

      > input {
        padding: 10px;
        width: 300px;
      }
    }

    > button {
      background: black;
      color: white;
      cursor: pointer;
      border: none;
      border-radius: 5px;
      padding: 10px 20px;
      margin-top: 20px;
    }

    > .error {
      margin-top: 5px;
      color: red;
    }

    > .success {
      margin-top: 5px;
      color: green;
    }
  }
}
